<template>
  <div class="flash-sale" v-if="saleList && saleList.length > 0">
    <div class="bg-tag"/>
    <div class="title">
      <span class="text">秒杀专区</span>
      <a href="javascript:;" @click="goPage()">更多 <i></i></a>
    </div>
    <div>
      <div class="horionzal-content">
        <div class="horionzal-row">
          <div class="time-item" v-for="(item, index) in saleList" :key="index" @click="changeTab(index)">
            <div class="time-text" :class="{active: currentTab === index}">
              <img v-if="item.dayStr==='往日'" src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/shop/previous-sale3x.png" class="time-img" alt="">
              <span v-else>{{item.timeStr}}</span>
            </div>
            <div class="time-status" :class="{active: currentTab === index}"><span>{{item.timeTip}}</span></div>
          </div>
        </div>
      </div>
      <div class="horionzal-content" style="margin-top: .16rem;">
        <div class="horionzal-row">
          <div class="prod-item" v-for="(item, index) in activeProductList" :key="index" @click="toProduct(item,index)">
            <img
              :src="item.imageUrl | imageToHttps | imageToWebp($webpSurpport)"
            />
            <div class="sale-price">￥{{item.flashsalePrice | formatPrice}}</div>
            <div class="origin-price">￥{{item.miniPrice | formatPrice}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  getStoreFlashSale,
} from '@/http/service/shop';
import { mapGetters } from 'vuex';
/** 秒杀专区 */
export default {
  inheritAttrs: false,
  props: {
    data: {
      type: Object,
      default: () => {},
    },
    storeId: {
      type: String,
      default: '',
    },
    storeSign: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      tabList: [1, 2, 3, 4, 5],
      prodList: [1, 2, 3, 4, 5, 6],
      currentTab: 0,
      saleList: [],
    };
  },
  computed: {
    ...mapGetters(['mId']),
    location() {
      return this.$store.getters['getlocationInfo']
    },
    activeProductList () {
      return this.saleList.length > this.currentTab ? this.saleList[this.currentTab].products : [];
    },
  },
  watch: {
    location(newVal) {
      this.getFlashSale();
    },
    mId() {
      this.getFlashSale();
    }
  },
  mounted() {
    this.getFlashSale();
  },
  methods: {
    getFlashSale() {
      getStoreFlashSale({
        provinceId: this.location.provinceId,
        cityId: this.location.cityId,
        districtId: this.location.districtId,
        streetId: this.location.townshipId,
        storeId: this.storeId
      }).then(res => {
        if (res.success) {
          const _result = res.data
          if (Array.isArray(_result.dates) && _result.dates.length) {
            let _list = []
            _result.dates.forEach(v => {
              _list.push(...v.list)
            })
            this.saleData = {
              ..._result.dates[0],
              list: _list,
              systemTime: _result.systemTime
            }
            this.handleSaleData(this.saleData);
          }
        } else {
          this.$toast(res.message)
        }
      })
    },
    handleSaleData(data) {
      if (!data.list || !data.list.length) {
        return
      }
      const { systemTime, list: _list } = data;

      let previousSale = {
        dayStr: '往日',
        timeTip: '进行中',
        promotionState: 1,
        isSaling: true,
        products: []
      }
      let commonList = []
      for (const item of _list) {
        if (item.dayStr === '往日') {
          if (item.products.length) {
            previousSale.products.push(...item.products)
          }
        } else {
          if (systemTime > item.endTime) {
            // 已结束
            item.promotionState = 2
            item.isSaling = false
          } else if (systemTime < item.startTime) {
            // 即将开始
            item.promotionState = 0
            item.isSaling = false
          } else {
            // 正在疯抢
            item.promotionState = 1
            item.isSaling = true
          }
          commonList.push(item)
        }
      }
      if (previousSale.products.length) {
        commonList = [previousSale, ...commonList]
      }

      let index = 0;
      for (var i = 0; i < commonList.length; i++) {
        let item = commonList[i];
        if (new Date(item.startTime).getDate() === new Date(systemTime).getDate()) {
          if (systemTime < item.endTime && systemTime > item.startTime) {
            index = i;
            break;
          }
        }
      }
      this.currentTab = index;
      this.saleList = commonList
    },
    changeTab(index) {
      this.currentTab = index;
    },
    goPage() {
      this.$util.gioTrackWithVariable('mall_store_click_model', {
        modelName: '秒杀专区',
        modelCode: 'seckill',
        site_number: 'null',
        itemCode: '更多',
      })
      this.$emit('changeBar', 2);
    },
    toProduct(prod, index) {
      this.$util.gioTrackWithVariable('mall_store_click_model', {
        modelName: '秒杀专区',
        modelCode: 'seckill',
        site_number: index + 1 + '',
        itemCode: prod.productId + '',
      })
      this.$router.push(`/goodsDetail?productId=${prod.productId}&visitStore=${this.storeId || ''}`);
    }
  },
}
</script>
