<template>
  <div :class="{'banner':true,'banner-img--custom':isCustom}" :style="isCustom? 'margin-top:0;' : ''" v-if="data.images && data.images.length">
    <van-swipe ref="vanSwiper" :autoplay="3000" :lazy-render="true" indicator-color="white" :class="{'swipe':true,'banner-img--custom':isCustom}">
      <van-swipe-item :class="{'swipe-item':true,'banner-img--custom':isCustom}" v-for="(item, index) in data.images" :key="index"  @click="bannerClick(item,index)">
        <img :class="{'swipe-img':true,'banner-img--custom':isCustom}" :src="item.imgUrl | imageToHttps"/>
      </van-swipe-item>
    </van-swipe>
  </div>
</template>
<script>
import commons from '@/utils/commons.js'
export default {
  props: {
    data: {
      type: Object,
      default: () => {},
    },
    storeId: {
      type: String,
      default: '',
    },
    isCustom: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
    };
  },
  mounted() {
    commons.swipeResize(this, 'vanSwiper')
  },
  methods: {
    bannerClick(item, index) {
      const { urlType, urlParam } = item;
      // 25093埋点
      this.$util.gioTrackWithVariable('mall_store_click_model', {
        modelName: '轮播图',
        modelCode: 'carousel',
        site_number: index + 1 + '',
        itemCode: 'null',
      })
      switch (Number(urlType || '')) { // 1自定义页面 2商品详情页 3领券中心 4海报页 5动态活动页面 6三翼鸟场景
        case 1: // 自定义页面
          if (urlParam) {
            this.$router.push(`/shopCustomPage?storeId=${this.storeId}&pageId=${urlParam}`);
          }
          break;
        case 2: // 商品详情页
          if (urlParam) {
            this.$router.push(`/goodsDetail?productId=${urlParam}&visitStore=${this.storeId}`);
          }
          break;
        case 3: // 领券中心
          this.$router.push(`/voucher?storeCouponid=${this.storeId}`);
          break;
        case 5: // 动态活动页
          if (urlParam) {
            this.$router.push(`/bannerCustom?id=${urlParam}`);
          }
          break;
        case 6: // 三翼鸟场景
          if (urlParam) {
            let url = urlParam + `&userId=${this.$store.state.ucId ? this.$store.state.ucId : ''}&userAccessToken=${this.$store.getters.getUserCenterToken}`
            this.$store.dispatch('goPageByType', {
              type: 5,
              params: this.$util.injectISOAddressCodeToURL(url, this.$store.getters.getlocationInfo)
            })
          }
          break;
      }
    }
  },
}
</script>
<style lang="scss" scoped>
.banner {
  margin: .1rem auto 0;
  height: 7.02rem;
  width: 7.02rem;
  overflow: hidden;
  margin-top: .22rem;
  transform: translateZ(0);
  will-change: auto;
}
.swipe{
  height: 7.02rem;
  width: 7.02rem;
  overflow: hidden;
  transform: translateZ(0);
  will-change: auto;
}
.swipe-item {
  width: 7.02rem;
  height: 7.02rem;
  .swipe-img{
    width: 7.02rem;
    height: 7.02rem;
    transform: translateZ(0);
    will-change: auto;
  }
}
.swipe ::v-deep .van-swipe__indicator {
  width: .26rem;
  height: .06rem;
  background: #ffffff;
  border-radius: .03rem;
  opacity: 0.52;
}
.swipe ::v-deep .van-swipe__indicator--active {
  opacity: 1;
}
.banner-img--custom{
  width:7.5rem !important;
  height:7.5rem !important;
}
</style>
